import { Col, Row } from 'antd';
import { graphql } from 'gatsby';
import parse from 'html-react-parser';
import React from 'react';
import Layout from '../components/Layout';
import SEO from '../components/SEO';
import Sidebar from '../components/Sidebar';
import TopSection from '../components/TopSection';
import { Post } from '../contracts/post';
import '../styles/blog.scss';
import { decodeHtmlCharCodes } from '../utils';

export interface Props {
	data: {
		wpPage: Post;
	};
	location: Location;
}

export const BlogPage = (props: Props) => {
	return (
		<>
			<TopSection title={decodeHtmlCharCodes(props.data.wpPage.title)} />
			<Layout location={props.location}>
				<SEO title={props.data.wpPage.title} description={props.data.wpPage?.excerpt} />
				<Row gutter={36} className="posts">
					<Col xs={24} sm={24} md={24} lg={16} xl={18} xxl={18} id="primary" className="content-area with-sidebar">
						<article className="post">
							<div className="post-content">{parse(props.data.wpPage.content)}</div>
						</article>
					</Col>
					<Col xs={0} sm={0} md={0} lg={8} xl={6} xxl={6} id="secondary" className="sidebar">
						<Sidebar />
					</Col>
				</Row>
			</Layout>
		</>
	);
};

export default BlogPage;

export const query = graphql`
	query($id: String!) {
		wpPage(id: { eq: $id }) {
			title
			slug
			content
			id
			modified
		}
	}
`;
